body {
  min-width: 100%;
  min-height: 100%;
  background-image: url(/portfolio/static/media/main.41c9500c.jpg);
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

body::after {
  content: "";
  position: fixed;
  z-index: -1;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    253deg,
    rgba(0, 0, 0, 0.4),
    rgba(255, 255, 255, 0.4),
    rgba(0, 0, 0, 0.4)
  );
  background-size: 200% 200%;
  -webkit-animation: Background 15s ease infinite;
          animation: Background 15s ease infinite;
}

.main {
  text-align: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.main__contact {
  font-size: 1.3em;
  flex-basis: 17%;
}

.main__name {
  font-family: "Mansalva", cursive;
  font-size: 4em;
  flex-basis: 40%;
}

.main__about {
  font-size: 1.5em;
  flex-basis: 10%;
}

.main__menu {
  flex-basis: 20%;
}

.main__button {
  margin: 3px;
  border: 2px solid #fff;
  border-radius: 25px;
  height: 50px;
  width: 130px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  letter-spacing: 3px;
  transition: 0.3s;
  cursor: pointer;
}

.main__button:hover {
  background-color: #fff;
  color: #000000;
}

.main__icon {
  padding: 10px;
  font-size: 18px;
  width: 40px;
  height: 40px;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
  border-radius: 50%;
  color: #fff;
}

.main__icon:hover {
  opacity: 0.7;
  color: #fff;
}

.main__icon--facebook {
  background: #125688;
}

.main__icon--instagram {
  background: #dd0045;
}

.main__icon--github {
  background: #555555;
}

@media (min-width: 767px) {
  .main__name {
    font-size: 5em;
  }
}

@-webkit-keyframes Fade {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-100%);
  }
}

@keyframes Fade {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-100%);
  }
}

@-webkit-keyframes Dotted {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes Dotted {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.welcome {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  -webkit-animation: Fade 1.3s ease;
          animation: Fade 1.3s ease;
  -webkit-animation-delay: 1.3s;
          animation-delay: 1.3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.welcome__text {
  font-family: "Mansalva", cursive;
  font-size: 6em;
  color: rgba(0, 0, 0, 0.6);
  z-index: 9999;
}

.welcome__dot {
  opacity: 0;
  -webkit-animation: Dotted 0.5s ease;
          animation: Dotted 0.5s ease;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.welcome__dot--1 {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

.welcome__dot--2 {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}

.nav {
  font-size: 1.2em;
  padding: 10px 0px 10px 0px;
  text-align: center;
}

.nav__list {
  display: flex;
  list-style: none;
  flex-direction: column;
}

.nav__item {
  margin: 0px 0px 15px 0px;
}

a {
  color: #9c9c9c;
  display: inline-block;
  text-decoration: none;
}

a:hover {
  color: black;
}

.nav__item .active {
  color: black;
}

.nav__item .active::before {
  content: ">";
  position: absolute;
  display: block;
  transform: translateX(-15px);
}

@media (min-width: 767px) {
  .nav {
    padding: 30px 0px 50px 0px;
  }

  .nav__list {
    flex-direction: row;
    justify-content: center;
  }

  .nav__item {
    margin: 0px 25px 0px 25px;
  }
}

.about {
  text-align: center;
}

.description {
  display: flex;
  flex-direction: column;
}

.description__img {
  position: relative;
  height: 300px;
  overflow: hidden;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.description__img img {
  opacity: 0.9;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
  min-width: 100%;
  min-height: 100%;
}

.description__text {
  margin-top: 30px;
  font-size: 1.2em;
}

.skills {
  margin: 30px auto 0px auto;
  width: 80%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.skills__skill {
  flex-basis: 30%;
  display: flex;
  flex-direction: column;
  padding-bottom: 30px;
}

.skills__skill:hover {
  color: black;
}

.skills__icon {
  padding-top: 10px;
  font-size: 3em;
}

.skills__icon--small {
  font-size: 2em;
}

@media (min-width: 767px) {
  .description {
    flex-direction: row;
  }

  .description__text {
    margin-top: 0px;
    flex-grow: 1;
  }

  .description__img {
    flex-basis: 35%;
    height: 700px;
  }

  .description__img img {
    transform: translate(-50%, -55%);
  }
}

@-webkit-keyframes ShowPhoto {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes ShowPhoto {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.gallery {
  /* margin: 0px auto 0px auto; */
  display: flex;
  flex-direction: column;
}

.gallery__img img {
  opacity: 0;
  width: 100%;
  height: auto;
}

@media (min-width: 767px) {
  .gallery {
    align-items: center;
    align-content: space-around;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .gallery__img {
    flex-basis: 33%;
    overflow: hidden;
  }
}

.projects {
  display: flex;
  flex-direction: column;
}

.card {
  margin: 0px auto 0px auto;
  margin-bottom: 30px;
  padding-bottom: 15px;
  width: 85%;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.card:hover {
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

