.card__img {
  overflow: hidden;
  height: auto;
}

.card__img img {
  width: 100%;
}

.card__description {
  text-align: left;
  padding: 10px;
}

.card__buttons {
  text-align: center;
}

.card__button {
  border: 1px solid #9c9c9c;
  border-radius: 8px 8px;
  padding: 5px 10px 5px 10px;
  margin: 0px 5px 0px 5px;
}

@media (min-width: 767px) {
  .projects {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .card {
    flex-basis: 45%;
  }
}

.contact {
  text-align: center;
}

.contact__header {
  margin: 0px auto 0px auto;
  position: relative;
  height: 300px;
  overflow: hidden;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.contact__header img {
  opacity: 0.9;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
  min-width: 100%;
  min-height: 100%;
}

.contact__email {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  cursor: pointer;
  color: white;
  margin: 20px auto 20px auto;
  display: block;
  line-height: 60px;
  width: 300px;
  background-color: rgb(223, 68, 68);
}

@media (min-width: 767px) {
  .contact__header {
    width: 80%;
  }
}

@-webkit-keyframes Show {
  0% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(0%);
  }
}

@keyframes Show {
  0% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(0%);
  }
}

.site {
  z-index: 10;
  -webkit-animation: Show 1.3s ease;
          animation: Show 1.3s ease;
  padding: 0 0 20px 0;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  color: #9c9c9c;
  background-color: rgb(255, 255, 255);
}

.site__arrow {
  color: rgba(0, 0, 0, 0.623);
  display: block;
  position: fixed;
  right: 30px;
  bottom: 10px;
  font-size: 1.5em;
  text-align: center;
  cursor: pointer;
  border: solid 1px #9c9c9c;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  background-color: rgba(255, 255, 255, 0.842);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.header {
  text-align: center;
  padding-top: 20px;
  font-family: "Mansalva", cursive;
  font-size: 3em;
}

hr {
  border: 0.7px solid #c5c5c5;
}

section {
  padding-top: 10px;
}

@media (min-width: 767px) {
  .site {
    box-shadow: 0px 0px 10px 4px rgba(110, 110, 110, 0.82);
    padding: 0 20px 20px 20px;
    width: 80vw;
    max-width: 1200px;
    min-width: 750px;
    margin: 20px auto 20px auto;
  }

  .site__arrow {
    display: none;
  }

  .header {
    padding-top: 40px;
  }

  section {
    padding-top: 30px;
  }
}


.fade-appear,
.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-appear-active,
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms linear 150ms;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 150ms linear;
}

