body {
  min-width: 100%;
  min-height: 100%;
  background-image: url("../images/main.jpg");
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

body::after {
  content: "";
  position: fixed;
  z-index: -1;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    253deg,
    rgba(0, 0, 0, 0.4),
    rgba(255, 255, 255, 0.4),
    rgba(0, 0, 0, 0.4)
  );
  background-size: 200% 200%;
  animation: Background 15s ease infinite;
}

.main {
  text-align: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.main__contact {
  font-size: 1.3em;
  flex-basis: 17%;
}

.main__name {
  font-family: "Mansalva", cursive;
  font-size: 4em;
  flex-basis: 40%;
}

.main__about {
  font-size: 1.5em;
  flex-basis: 10%;
}

.main__menu {
  flex-basis: 20%;
}

.main__button {
  margin: 3px;
  border: 2px solid #fff;
  border-radius: 25px;
  height: 50px;
  width: 130px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  letter-spacing: 3px;
  transition: 0.3s;
  cursor: pointer;
}

.main__button:hover {
  background-color: #fff;
  color: #000000;
}

.main__icon {
  padding: 10px;
  font-size: 18px;
  width: 40px;
  height: 40px;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
  border-radius: 50%;
  color: #fff;
}

.main__icon:hover {
  opacity: 0.7;
  color: #fff;
}

.main__icon--facebook {
  background: #125688;
}

.main__icon--instagram {
  background: #dd0045;
}

.main__icon--github {
  background: #555555;
}

@media (min-width: 767px) {
  .main__name {
    font-size: 5em;
  }
}
